body,
html {
  /* font-family: "Quicksand", sans-serif; */
  font-family: "Bellota Text", cursive;
}

.jumbotron {
  height: 650px;
}

.wavy {
  text-decoration: underline;
}

.font2 {
  font-family: "Bellota Text", cursive;
}

#chevron {
  position: relative;
  text-align: center;
  padding: 12px;
  margin-bottom: 6px;
  height: 60px;
  width: 200px;
}
#chevron:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 51%;
  background: red;
  transform: skew(0deg, 6deg);
}
#chevron:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  width: 50%;
  background: red;
  transform: skew(0deg, -6deg);
}
.center-object {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.navbar-brand {
  color: darkred;
}

.alert-dark {
  background-color: #999da1;
  color: snow;
}

.peach-gradient {
  background: linear-gradient(to right, #e44d2c, #f16529);
  color: #fff;
}

.yellow-gradient {
  background: linear-gradient(to right, #fdc830, #f37335, #f27121);
}

.blue-gradient {
  background: linear-gradient(to right, #00d2ff, #3a7bd5);
}

.nelson-gradient {
  background: linear-gradient(to right, #f2709c, #ff9472);
}

.orange-gradient {
  background-color: #fbb034;
  background-image: radial-gradient(315deg, #fbb034 0%, #ffdd00 74%);
}

.borderless {
  border-radius: 0;
  border: 2px solid black;
  color: black;
}

.nav-item:hover {
  border-bottom: 2px solid lightblue;
  transition: ease-in-out 0.1s;
  color: darkred;
}

.btn-outline-white {
  border-radius: 0px;
  color: white;
  border: 2px solid white;
}

.round-border {
  border-radius: 1em;
}
